/* Variaveis */
$blackColor: #242424;
$grayColor: #828282;
$gray2Color:#5D5D5D;
$lightGrayColor: #cccccc;
$redColor: #a21010;
$darkRedColor: #820d0d;
$whiteColor: #ffffff;
$whiteSColor: #ffffff;

$grayBackgroud: #F0F5F9;

$fontSizeXxl: 55px;
$fontSizeXl: 40px;
$fontSizeLg: "";
$fontSizeMd: 24px;
$fontSizeSm: 18px;
$fontSizeXs: 16px;
$fontSizeXxs: 12px;


/* Reset */
html,
body{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

*{
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
    padding:0;
}

ul,ol{
    list-style: none;
}

a {
    text-decoration: none;
}

img{
    display: block;
}


/* Geral */
.animate-box{
    opacity: 0;
    visibility: hidden;
}

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;

    @media (min-width: 768px) {
        width:750px;
    }
    
    @media (min-width: 992px) {
        width:970px;
    }
    
    @media (min-width: 1200px) {
        width:1170px;
    }
}

body {
    font-family: 'Open Sans', sans-serif;
}

.align-center {
    text-align: center;
}

.grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
}

.grid__item{
    width: 100%;

    &:hover{
        .grid__box {
            color: $whiteColor;
            background-color: $redColor;
            box-shadow: 0px 18px 71px -10px rgba(0, 0, 0, 0.75) ;
            transition: 0.5s;
            top: -10px;
        }

        .grid__title{
            color: $whiteColor;
        }

        .grid__description{
            color: $whiteColor;
        }

        .grid__box--prod{
            top: 0;
            background-color: $whiteColor;
            border: 1px solid rgba(0,0,0,0.6);

            &:after{
               display: block;
            }
        }

        .grid__info{
            display: block;
        }

        .grid__icon i {
            color: rgba(255, 255, 255, 0.7);
        }
    }

    &--prod{
        width: auto;
        margin: auto;        
    }

    @media (min-width: 768px) {
        width: 50%;
    }
    
    @media (min-width: 992px) {
        width: 33%;
    }    
}

.grid__box {
    margin-bottom: 10px;
    padding: 26px;
    position: relative;

    &--prod {
        margin: 0 0 30px 0;        
        padding: 0;     
        border: 1px solid $grayBackgroud; 

        @media(min-width: 768px){
            margin: 0 30px 30px 0; 
        }
    }
}

.grid__info {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 1;
    padding: 15px 20px;
    background-color: rgba(0,0,0,0.6);
}

.grid__info-text{
    padding-top: 10px;
    clear: both;
}

.grid__info-img{
    float: left;
}

.grid__info-title{
    float: left;
    padding: 3px 10px;
    font-family: 'Righteous', cursive;
    color: $whiteColor;
    font-size: 20px;
}

.grid__title {
    margin-bottom: 30px;  
    color: $grayColor;  
}

.grid__description {
    margin-bottom: 30px;
    line-height: 30px;   
    color: $grayColor;
}

.grid__icon {
    margin-bottom: 20px;
    display: block;
}

.section-services {
    padding-bottom: 48px;

    @media(min-width: 992px){
        padding-bottom: 96px;
    }
}

/* Temporario */
img{
    max-width: 100%; 
}

.our-process__img { 
    max-width: 100%;
    margin-bottom: 48px;   
}

.banner{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    
    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(42, 51, 86, 0.6);
    }
}

.banner__video{    
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: absolute;   
}

.banner__content{
    position: relative;
    z-index: 3;
}

.banner__title{
    font-size: $fontSizeXl ;
    color: $whiteSColor;
    font-weight: 300;
    line-height: 55px;
    margin: 0 0 40px;  

    @media(min-width: 750px){
        font-size: $fontSizeXxl ;
        line-height: 65px;
    }

    @media(min-width: 1200px){
        max-width: 61%;

    }
}

.title__content {
    padding: 48px 0;

    @media(min-width: 992px){
        padding: 96px 0;
    }
}

.banner__scroll{
    display:flex;
    justify-content:center;
    align-items:flex-end;
    width:100vw;
    height:100vh;
    position: absolute;
    bottom: 40px;
    z-index: 2;
  
}

@keyframes scroll-down-pulse {
    from { transform:translateY(-8px); opacity: .3; }
    50% { transform:translateY(8px); opacity: 1; }
    to { transform:translateY(-8px); opacity: .3; }
}
.banner__scroll-item{
position:relative;
border:2px solid white;
width:1rem;
height:2rem;
border-radius:15px;
animation: scroll-down-pulse 3s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite;
}
.banner__scroll-item:before{
display:block;
content:'';
box-sizing:border-box;
width:0;
height:25%;
border-right:2px solid white;  
position:absolute;
top:.3rem;
left:50%;
margin-left:-1px;
}
.banner__scroll-item:after{
content:'';
display:block;
width:.5rem;
height:.5rem;
border-right:2px solid white;  
border-bottom:2px solid white;  
position:absolute;
bottom:-1rem;
left:50%;
margin-left:-.325rem;
transform:rotate(45deg);
}

.title__xxs{
    font-size: $fontSizeXxs;
    color: $lightGrayColor;

    &--Up{
        text-transform: uppercase;
    }

    &--white{
    color: $whiteColor;
    }
}

.title__xs{
    font-size: $fontSizeXs;
    color: $grayColor;

    &--white{
        color: $whiteColor;
   }
}

.title__xl{
    font-size: $fontSizeXl;
    font-weight: 300;

    &--mv{
        margin: 20px 0 15px 0;
    }

    &--white{
        color: $whiteColor;
        }

    @media(min-width: 750px){
        font-size: $fontSizeXxl ;
        line-height: 65px;
    }
}

.title__md {
    font-size: $fontSizeMd;
    font-weight: 400;
    color: $gray2Color;
}

.button {
    text-decoration: none;
    color: $whiteSColor;   
    padding: 15px 30px;
    background: $redColor;

    border: none;
    font-size: $fontSizeSm;
    text-transform: uppercase;
    border-radius: 8px;
    
    &:hover {
        background-color: $darkRedColor;
        transition: 0.5s;
        box-shadow: 0px 14px 20px -9px rgba(0, 0, 0, 0.75) 
    }

    &--xs{
        padding: 8px 30px;
        font-size: $fontSizeXs;
        display: table;
        margin: 20px auto 0 auto;
    }
}

.bg-gray{
    background-color: $grayBackgroud;
}

.footer__content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.footer__title {
    margin-bottom: 15px;
    margin-top: 25px;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.footer__item {
    width: 50%;
    margin-top: 15px;

    &:nth-child(1n+2){
        padding: 65px 0 0 10%;
        @media(max-width: 767px){
            padding: 20px 0 0 0;
        }
    }

    @media(min-width: 768px){
        width: 33%;
    }


    @media(max-width: 767px){
        &--xl{
            width: 100%;
        }
    }
}

.footer__socialmedia {
    color: #a21010;
    padding-right: 10px;
    font-size: $fontSizeMd;
    
    &--bk {
        background: $redColor;
        color: $whiteColor;
        border-radius: 4px;
        width: 23px;
        height: 23px;
        display: inline-block;
        top: 1px;
        position: relative;

        i {
        font-size: 18px;
        line-height: 18px;
        top: 2px;
        position: absolute;
        left: 3px;
        }   
    }  
}

.footer__text {
    color: $grayColor;
    line-height: 26px;
    font-style: normal;
}

.footer__link{
    color: $blackColor;
    padding-bottom: 5px;
    display: block;

    &--red {
        color: $redColor;
    }
}

.footer__menu {

    @media(max-width:520px) {
        float: right;
        padding-right: 15%;
    }
}

.footer__copyright {
    font-size: $fontSizeXxs;
    text-align: center;
    color: $grayColor;
    padding: 20px;
    border-top: 1px solid $grayBackgroud;
    margin-top: 20px;
}

.go-top {
     position: fixed;
     bottom: 20px;
     right: 20px;
     z-index: 4;
     opacity: 0;
     visibility: hidden;
     

     &.active{
         opacity: 1;
         visibility: visible;
     }
 }

 .go-top__link{
    width: 50px;
    height: 50px;
    display: table;
    background: rgba(0, 0, 0, 0.5);
    color: $whiteColor;
    text-align: center;
    border-radius: 4px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
 }

 .menu{
    position: fixed;
    z-index: 4;
    width: 100%;
    padding: 10px 0;    
    height: 74px;
    display: flex;

    @media(min-width:768px){
        li{        
            a{ 
                color: rgba(255, 255, 255, 0.5);
                font-size: 16px;
                padding: 30px 10px;
                transition: 0.5s;
                display: inline-block; 
            }
    
            &.active a{
                color:$whiteColor;
            }

        }
    }   

    &.scrolled{
        background: $whiteColor;
        padding: 0;
        box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.15);

        @media(min-width:768px){
            li{ 
                &.active a{
                    color: $redColor;
                }
                
                a{
                    color: $blackColor;
                    padding: 26px 10px;
                }
            }
        }

        .menu__title{
            color: $blackColor;
        }

        .menu__toggle{
            background: $blackColor;
        }
    }
}

.menu__item{
    display: flex;
    align-items: center;
    justify-content: space-between;    

    @media(max-width: 768px){
        width: 100%;

        &.opened{
            .menu__wrapper{
                display: block;
                position: fixed;
                height: 100vh;
                width: 100vw;
                background: rgba(0,0,0,0.7);
                top: 0;
                left: 0;
            }

            .menu__close{
                color: $whiteColor;
            }

            .menu__content{
                flex-direction: column;
                justify-content: center;
                align-items: center;
                height: 100%;

                li{
                    width: 100%;
                    text-align: center;

                    &.active{
                        background-color: $whiteColor;

                        a{
                            color: $blackColor;
                        }
                        
                    }
                   
                    a{
                        color: $whiteColor;
                        font-size: $fontSizeMd;
                        padding: 10px;
                        display: block;

                        &.menu__lang{
                            display: inline-block;
                        }
                    }
                }
            }  
            
            .menu__toggle {
                opacity: 1;
                transform: rotate(45deg) translate(-2px, -1px);
                background: $whiteColor;
            }

            .menu__btn{
                span:nth-of-type(2) {
                    opacity: 0;
                    transform: rotate(0deg) scale(0.2, 0.2);
                }

                span:nth-of-type(3) {
                    transform: rotate(-45deg) translate(0, -1px);
                    margin-top: 7px;
                }
            }
        }
    }
}

.menu__btn{
    position: relative;
    z-index: 4;

    @media(min-width:768px){
        display: none;
    }

    span:first-child {
        transform-origin: 0% 0%;
    }
    
    span:nth-of-type(2) {
        transform-origin: 0% 100%;
    }
    
    span:nth-of-type(3) {
        transform-origin: 0% 100%;
    }
}

.menu__content{
    display: flex;
    flex-direction: row;    
}

.menu__content-logo{
    display: flex;
    justify-content: center;
}

.menu__logo{
    max-width: 43px;
}

.menu__title{
    padding: 5px 10px;
    color:  $whiteColor;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: bold;
}

.menu__wrapper{
    display: none;

    @media(min-width:768px){
        display: block;
    }
}

.menu__toggle {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;

    background: $whiteColor;
    border-radius: 3px;
    z-index: 999;

    transform-origin: 4px 0px;

    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    opacity 0.3s ease;
} 

.grid__icon i {
    font-size: 40px;
    color: rgba(42, 54, 59, 0.2);
}

.loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: url(../images/loader.gif) center no-repeat #fff;
}
